const LanguagePack = props => {
	let lang = props?.lang?.slice(0, 2).toLowerCase() ?? 'en'
	if (!translation[props.text])
		return '';
	if (!translation[props.text][lang])
		lang = 'en';
	return translation[props.text][lang];
}

export default LanguagePack;